import React, { Component } from 'react'
import {
    Card,
    Table,
    Modal,
    message,
    Button,
} from 'antd'
import {
    PlusOutlined,
    ExclamationCircleOutlined
} from '@ant-design/icons'
import UserForm from './UserForm'
import LinkButton from '../../components/LinkButton'
import { PAGE_SIZE } from '../../utils/constants'
// 
import { formateDate } from '../../utils/dateUtils'
import {
    reqRole,
    reqUser,
    reqAddUpdateUser,
    reqDelectUser
} from '../../api'

const { confirm } = Modal;
export default class User extends Component {
    state = {
        users: [],
        loading: true,
        confirmLoading: false,       // loading
        visible: false,         // 
        roleList: []
    }
    formRef = React.createRef();
    // 
    getUser = async () => {
        try {
            const response = await Promise.all([reqUser(), reqRole()]);
            let { status, data: { users } } = response[0];
            let { data } = response[1];
            const roleList = data.map(role => {
                const { _id, name } = role;
                return { _id, name }
            });
            // roleListstatefind(role_name)
            this.setState({ roleList }, () => {
                if (status === 0 && users.length > 0) {
                    // user
                    users = users.map(user => {
                        user.create_time = formateDate(user.create_time);
                        const role = this.state.roleList.find(role => role._id === user.role_id);
                        // undefined
                        user.role_name = role === undefined ? null : role.name;
                        return user
                    })
                    this.setState({ users, loading: false });
                } else {
                    message.error('');
                }
            })
        } catch (err) {
            console.log(err)
        }
    }

    // 
    showAdd = () => {
        this.user = null
        this.setState({ visible: true });
    }
    // 
    showUpdate = (user) => {
        this.user = user;
        this.setState({ visible: true });
    }

    // 
    showDelete = (user) => {
        // thisonOk()
        const { handleDelete } = this;
        confirm({
            title: `${user.username}`,
            icon: <ExclamationCircleOutlined />,
            content: '!',
            maskClosable: true,
            onOk() {
                handleDelete(user);
            },
        });
    }

    // /
    handleAddUpdate = async () => {
        try {
            // this.formRef.current
            // this.formRef.currentUserFormformRef
            const user = await this.formRef.current.formRef.current.validateFields();
            let msg = ''
            // 
            if (this.user) {
                user._id = this.user._id;
                msg = ''
            }
            const { status } = await reqAddUpdateUser(user);

            if (status === 0) {
                this.getUser();
                message.success(msg + '');
            } else {
                message.error(msg + '');
            }

        } catch (err) {
            console.log(err)
            message.error('');
        }
        this.setState({ visible: false })
    }
    // 
    handleDelete = async (user) => {
        const { status } = await reqDelectUser(user._id);
        if (status === 0) {
            this.getUser();
            message.success('');
        } else {
            message.error('')
        }
    }

    // 
    handleCancel = () => {
        this.setState({ visible: false })
    }
    UNSAFE_componentWillMount() {
        this.columns = [
            {
                title: '',
                dataIndex: 'username',
                key: 'username'
            },
            {
                title: '',
                dataIndex: 'email',
                key: 'email'
            },
            {
                title: '',
                dataIndex: 'phone',
                key: 'phone'
            },
            {
                title: '',
                dataIndex: 'create_time',
                key: 'create_time'
            },
            {
                title: '',
                dataIndex: 'role_name',
                key: 'role_name'
            },
            {
                title: '',
                render: (user) => (
                    <>
                        <LinkButton onClick={() => { this.showUpdate(user) }}></LinkButton>
                        <LinkButton onClick={() => { this.showDelete(user) }}></LinkButton>
                    </>
                )

            },
        ]
    }
    componentDidMount() {
        this.getUser();
    }
    render() {
        const { users, confirmLoading, visible, roleList, loading } = this.state;
        let { columns } = this
        const user = this.user || {}
        const title = (
            <Button type='primary' ghost onClick={this.showAdd}>
                <PlusOutlined />
                
            </Button>
        )

        return (
            <Card title={title}>
                <Table
                    bordered
                    dataSource={users}
                    columns={columns}
                    rowKey='_id'
                    loading={loading}
                    pagination={{
                        pageSize: PAGE_SIZE,
                        showQuickJumper: true,
                    }}
                >

                </Table>
                {
                    this.state.visible &&
                    <Modal
                        title={user ? '' : ''}
                        visible={visible}
                        onOk={this.handleAddUpdate}
                        confirmLoading={confirmLoading}
                        onCancel={this.handleCancel}
                    >
                        <UserForm user={user} ref={this.formRef} roleList={roleList} />
                    </Modal>
                }

            </Card>

        )
    }
}
