import React, { Component } from 'react'
import { Card } from 'antd'
import ReactEcharts from 'echarts-for-react'

/*

 */
export default class Pie extends Component {

  getOption = () => {
    return {
      title: {
        text: '',
        subtext: '',
        x: 'center'
      },
      tooltip: {
        trigger: 'item',
        formatter: "{a} <br/>{b} : {c} ({d}%)"
      },
      legend: {
        orient: 'vertical',
        left: 'left',
        data: ['', '', '', '', '']
      },
      series: [
        {
          name: '',
          type: 'pie',
          radius: '55%',
          center: ['50%', '60%'],
          data: [
            { value: 335, name: '' },
            { value: 280, name: '' },
            { value: 204, name: '' },
            { value: 135, name: '' },
            { value: 408, name: '' }
          ],
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }
      ]
    };

  }

  getOption2 = () => {
    return {
      legend: {
        top: 'bottom',
        lineStyle: {
          width: '20'
        }
      },
      toolbox: {
        show: true,
        feature: {
          mark: { show: true },
          dataView: { show: true, readOnly: false },
          restore: { show: true },
          saveAsImage: { show: true }
        }
      },
      series: [
        {
          name: '',
          type: 'pie',
          radius: [20, 100],
          center: ['50%', '50%'],
          roseType: 'area',
          itemStyle: {
            borderRadius: 3
          },
          data: [
            { value: 335, name: '' },
            { value: 280, name: '' },
            { value: 204, name: '' },
            { value: 135, name: '' },
            { value: 408, name: '' }
          ],
          labelLine: {
            length: 5
          }
        }
      ]
    }
  }

  render() {
    return (
      <div>
        <Card title=''>
          <ReactEcharts option={this.getOption()} style={{ height: 300 }} />
        </Card>
        <Card title=''>
          <ReactEcharts option={this.getOption2()} style={{ height: 350 }} />
        </Card>
      </div>
    )
  }
}
