import React, { Component } from 'react'
// antd
import {
    Card,
    Form,
    Select,
    Input,
    Button,
    Table,
    message
} from 'antd'
import {
    PlusOutlined
} from '@ant-design/icons'
// 
import LinkButton from '../../components/LinkButton';
import {
    reqProuductList,
    reqSearchProduct,
    reqUpdateProductState,
} from '../../api'
// 
import { PAGE_SIZE } from '../../utils/constants'
import './index.less'

const { Option } = Select;
const { Search } = Input;

export default class ProductHome extends Component {
    searchRef = React.createRef();

    state = {
        pageLoading: true,      // 
        pageNum: 1,             // 
        total: 0,               // 
        productList: [],         // 
    }
    // 
    getProduct = async (pageNum, pageSize) => {
        try {
            const {searchType, searchName} = await this.searchRef.current.validateFields()
            let products
            // searchProductreqProduct
            if (searchName !== undefined) {
                products = await reqSearchProduct({pageNum, pageSize, searchType, searchName});
            } else {
                products = await reqProuductList(pageNum, pageSize);
            }
            if (products.status === 0) {
                const { pageNum, total, list } = products.data;
                this.setState({ 
                    pageNum, 
                    total, 
                    productList: list, 
                    pageLoading: false 
                })
            } else {
                message.error('');
            }
        } catch(err) {
            // catch this.searchRef.current.validateFields() 
            // reqajax
            message.error('');
        }

    }

    // 
    withdrawProduct = async (productId, status) => {
        const {pageNum} = this.state;
        const response = await reqUpdateProductState(productId, (status === 1 ? 0 : 1));
        if (response.status === 0) {
            this.getProduct(pageNum, PAGE_SIZE);
            message.success('');
        } else {
            message.error('');
        }
    }
    // Table
    pageChange = (pageNum, pageSize) => {
        this.getProduct(pageNum, pageSize);
    }

    // 
    UNSAFE_componentWillMount() {
        this.columns = [
            {
                title: '',
                dataIndex: 'name',
                key: 'name'
            },
            {
                title: '',
                dataIndex: 'desc',
                key: 'desc',
                width: '40%'
            },
            {
                title: '',
                dataIndex: 'price', 
                key: 'price'
            },
            {
                title: '',
                key: 'state',
                width: '13%',
                render: (product) => (
                    <>
                        <span style={{fontSize: 13, width: 50, marginBottom: '5px', display: 'inline-block'}}>{product.status ? '' : ''}</span>
                        <Button type='primary' style={{fontSize: 12, width: 50, height: 30, padding: 0}} onClick={() => {this.withdrawProduct(product._id, product.status)}}>{product.status ? '' : ''}</Button>
                    </>
                )
            },
            {
                title: '',
                key: 'operate',
                render: (product) => (
                    <>
                        {/* push() path  state */}
                        {/* push statepath this.props.loaction.state */}
                        <LinkButton onClick={() => this.props.history.push('/product/detail', product)}></LinkButton>
                        <LinkButton onClick={() => this.props.history.push('/product/addupdate', product)}></LinkButton>
                    </>
                )
            },
        ]
    }
    // 
    componentDidMount = () => {
        this.getProduct(this.state.pageNum, PAGE_SIZE);
    }
    render() {
        // state
        const { pageLoading, total, productList } = this.state;
        
        const title = (
            <Form ref={this.searchRef} layout="inline">
                <Form.Item name='searchType' initialValue="productName">
                    <Select style={{ width: 120 }}>
                        <Option value="productName"></Option>
                        <Option value="productDesc"></Option>
                    </Select>
                </Form.Item>
                <Form.Item name="searchName" style={{ width: 120 }}>
                    <Search placeholder="" allowClear style={{ width: 150 }}  onSearch={() => {this.getProduct(1, PAGE_SIZE)}} />
                </Form.Item>
            </Form>
        )
        // card
        const extra = (
            <Button style={{ width: 90, padding: '4px 0px' }} onClick={this.showAdd} type='primary' ghost>
                <PlusOutlined />
                <span onClick={() => this.props.history.push('/product/addupdate')}></span>
            </Button>
        )
        return (
            <div>
                <Card title={title} extra={extra} >
                    <Table
                        dataSource={productList}
                        columns={this.columns}
                        rowKey='_id'
                        bordered
                        loading={pageLoading}
                        pagination={{
                            total: total,
                            pageSize: PAGE_SIZE,
                            showQuickJumper: true,
                            onChange: (pageNum) => {this.pageChange(pageNum, PAGE_SIZE)}       // PageSize
                        }}
                    />
                </Card>
            </div>
        )
    }
}
