import React, { Component } from 'react';
import {connect} from 'react-redux';
import {
    Card,
    Table,
    Button,
    Modal,
    Form,
    Input,
    Tree,
    message
} from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import { PAGE_SIZE } from '../../utils/constants';
import { reqAddRole, reqRole, reqUpdateRole } from '../../api';
import { formateDate } from '../../utils/dateUtils';
import { logout } from '../../redux/action';
import menuList from '../../config/menuConfig.jsx';
// auth_nameredux
// import memoryUtils from '../../utils/memoryUtils';

class Role extends Component {
    state = {
        roles: [],           // 
        currentRole: {},     // 
        menuList: [{
            title: '',
            key: '/',
            children: menuList
        }],
        disabled: true,      // 
        confirmLoading: false,  // loading
        loading: true,
        visible: false,             // 0:1:2:
        expandedKeys: []            // Tree
    }

    // role = {};

    formRef = React.createRef();

    getRoles = async () => {
        const { status, data } = await reqRole();
        if (status === 0 && data.length >= 0) {
            const roles = data.map(role => {
                role.create_time = formateDate(role.create_time);
                role.auth_time = formateDate(role.auth_time);
                return role;
            })
            this.setState({ roles, loading: false })
        }
    }
    showAdd = () => {
        this.setState({ visible: 1 });
    }
    showSetAthu = () => {
        this.setState({ visible: 2 });
    }
    handleCancel = () => {
        this.setState({ visible: false })
    }
    // 
    handleAdd = async () => {
        try {
            const { roleName } = await this.formRef.current.validateFields();
            this.setState({ confirmLoading: true })
            const { status } = await reqAddRole(roleName);
            if (status === 0) {
                // 
                this.getRoles();
                // 
                this.setState({ confirmLoading: false, visible: false });
                message.success('');
            } else {
                message.error('');
            }
        } catch {
            message.error('');
        }
    }
    // 
    handleUpdate = async () => {
        this.setState({ confirmLoading: true })

        const auth_name = this.props.user.username;
        const auth_time = new Date().getTime();
        let {currentRole: {_id, menus}} = this.state;
        const role = {_id, menus, auth_name, auth_time};

        const {status} = await reqUpdateRole(role);
        if (status === 0) {
            // 
            if (role._id === this.props.user.role_id) {
                this.props.logout();
                message.success(', ');
            } else {
                this.getRoles();
                this.setState({ confirmLoading: false, visible: false });
                message.success('');
            }
        } else {
            message.error('');
        }
    }
    // Tree
    handleCheck = (menus) => {
        const {currentRole} = this.state;
        this.setState({currentRole:{...currentRole, menus}})
    }
    // 
    setExpandedKeys = () => {
        const {currentRole: {menus}} = this.state;
        const expandedKeys = [];
        if (menus.length > 0) {
            expandedKeys.push('/');
            menus.map(menu => {
                if (menu.indexOf('/charts') === 0) {
                    expandedKeys.push('/charts');
                } else if (menu === '/product' || menu === '/category') {
                    expandedKeys.push('/products');
                }
                return expandedKeys;
            });
        }
        this.setState({expandedKeys});
    }
    UNSAFE_componentWillMount() {
        this.columns = [
            {
                title: '',
                dataIndex: 'name',
                key: 'name'
            },
            {
                title: '',
                dataIndex: 'create_time',
                key: 'create_time'
            },
            {
                title: '',
                dataIndex: 'auth_time',
                key: 'auth_time'
            },
            {
                title: '',
                dataIndex: 'auth_name',
                key: 'auth_name'
            },
        ];
    }
    componentDidMount() {
        this.getRoles();
    }
    render() {
        const { roles, loading, disabled, visible, confirmLoading, currentRole, menuList, expandedKeys } = this.state
        const { columns, formRef } = this;
        const title = (
            <div>
                <Button type='primary' ghost onClick={this.showAdd}>
                    <PlusOutlined />
                    
                </Button>
                &nbsp; &nbsp;
                <Button disabled={disabled} type='primary' ghost onClick={this.showSetAthu}>
                    
                </Button>
            </div>
        )
        return (
            <Card title={title}>
                <Table
                    bordered
                    dataSource={roles}
                    columns={columns}
                    rowKey='_id'
                    loading={loading}
                    pagination={{
                        pageSize: PAGE_SIZE,
                        showQuickJumper: true,
                    }}
                    rowSelection={{
                        type: 'radio',
                        onChange: (selected, selectedRows) => {
                            // selectedRowKeys: key, selectedRows
                            // 
                            this.setState({ disabled: false, currentRole: selectedRows[0] }, () => {
                                // 
                                this.setExpandedKeys()
                            });
                        }
                    }}

                >

                </Table>
                {/*  */}
                <Modal
                    title={''}
                    visible={visible === 1}
                    onOk={this.handleAdd}
                    confirmLoading={confirmLoading}
                    onCancel={this.handleCancel}
                >
                    <Form
                        ref={formRef}
                        wrapperCol={{
                            span: 12,
                        }}

                    >
                        <Form.Item
                            label=''
                            name='roleName'
                            rules={[
                                {
                                    required: true,
                                    message: ''
                                }
                            ]}
                        >
                            <Input placeholder=''></Input>
                        </Form.Item>
                    </Form>
                </Modal>

                {/*  */}
                {
                    visible &&
                    <Modal
                        title={''}
                        visible={visible === 2}
                        onOk={this.handleUpdate}
                        confirmLoading={confirmLoading}
                        onCancel={this.handleCancel}
                    >
                        <Form
                            wrapperCol={{
                                span: 12,
                            }}
                        >
                            <Form.Item
                                label=''
                                name='roleName'
                                initialValue={currentRole.name}
                            >
                                <Input disabled></Input>
                            </Form.Item>
                        </Form>
                        <Tree
                            checkable
                            expandedKeys={expandedKeys} // 
                            onCheck={this.handleCheck}
                            defaultCheckedKeys={currentRole.menus} // 
                            treeData={menuList}             // 
                        />
                    </Modal>
                }
            </Card>
        )
    }
}

export default connect(
    state => ({user: state.user}),
    {
        logout
    }
)(Role);