import React, { Component } from 'react'
import {
    Card,
    Table,
    Button,
    Modal,
    Form,
    Select,
    Input,
    message
} from 'antd'
import { PlusOutlined, ArrowRightOutlined } from '@ant-design/icons';
// 
import { reqCategory, reqAddCategory, reqUpdateCategory } from '../../api';
// LinkButton
import LinkButton from '../../components/LinkButton'

// selectoption
const { Option } = Select;
export default class Category extends Component {
    state = {
        dataSource: [],         // 
        subCategory: [],        // 
        parentId: '0',          // Id
        parentName: '',          // 
        loading: true,          // loading
        showLinkButton: 0,      // 0: 12
        confirmLoading: false   // Madalloading
    }
    // addRef
    addRef = React.createRef()
    updateRef = React.createRef()

    // 
    getCategory = async (parentId) => {
        // parentIdstate
        parentId = parentId || this.state.parentId;
        const category = await reqCategory(parentId);
        if (category.status === 0) {
            // Id0
            if (parentId === '0') {
                this.setState({ dataSource: category.data })
            } else {
                this.setState({ subCategory: category.data })
            }
            this.setState({ loading: false });
        }
    }

    // 
    showsubCates = (category) => {
        // getCategorysetStatesetState
        this.setState({
            parentId: category._id,
            parentName: category.name
        }, () => {
            this.getCategory();
        })
    }
    // 
    showCategory = () => {
        this.setState({
            parentId: '0',
            parentName: '',
            subCategory: [],
            // showStatus: 0
        })
    }


    // 
    showAdd = () => {
        this.setState({ showLinkButton: 1 });
    }
    // 
    handleAdd = async () => {
        try {
            // 
            const { parentId, categoryName } = await this.addRef.current.validateFields()
            this.setState({ confirmLoading: true })
            // 
            reqAddCategory(parentId, categoryName);
            if (parentId === this.setState.parentId) {
                this.getCategory()
            } else if (parentId === '0') {
                this.getCategory(parentId)
            }
            message.success("")
            this.setState({ confirmLoading: false, showLinkButton: 0 })
        } catch {
            message.error('')
        }
    }

    // 
    showUpdate = (category) => {
        //  this
        this.category = category;
        this.setState({ showLinkButton: 2 })
    }
    // 
    handleUpdate = async () => {
        // id
        const categoryId = this.category._id
        // 
        const { categoryName } = await this.updateRef.current.validateFields()
        this.setState({ confirmLoading: true })
        try {
            await reqUpdateCategory({ categoryId, categoryName });
            // 
            this.getCategory();
            // 
            this.updateRef.current.resetFields()
            this.setState({ showLinkButton: 0, confirmLoading: false })
            message.success('')

        } catch (err) {
            console.log(err)
            message.error('~');
        }

    }


    // 
    handleCancel = () => {
        this.setState({ showLinkButton: 0 })
    }

    // 
    UNSAFE_componentWillMount() {
        // 
        this.columns = [
            {
                title: '',
                dataIndex: 'name',
                key: 'name',
            },
            {
                title: '',
                width: '30%',
                render: (category) => {
                    return (
                        <div>
                            <LinkButton onClick={() => { this.showUpdate(category) }}></LinkButton>
                            {
                                this.state.parentId === '0' ?
                                    <LinkButton onClick={() => { this.showsubCates(category) }}></LinkButton> : null
                            }
                        </div>
                    )
                }
            }]
    }

    // 
    componentDidMount() {
        this.getCategory();
    }

    render() {
        // state
        const { dataSource, subCategory, parentId, parentName, loading, confirmLoading, showLinkButton } = this.state;
        // Card
        // Card 
        const title = parentId === '0' ? '' : (
            <span>
                <LinkButton style={{ fontSize: '16px' }} onClick={this.showCategory}></LinkButton>
                <ArrowRightOutlined />&nbsp;&nbsp;&nbsp;
                <span style={{ fontSize: '14px' }}>{parentName}</span>
            </span>
        )
        // card
        const extra = (() => {
            return (
                <Button onClick={this.showAdd} type='primary' ghost>
                    <PlusOutlined />
                    <span></span>
                </Button>
            )
        })()


        return (
            <Card title={title} extra={extra}>
                {/* dataSourcecolumns */}
                <Table
                    bordered
                    loading={loading}
                    rowKey='_id'
                    dataSource={parentId === '0' ? dataSource : subCategory}    // 
                    columns={this.columns}
                    pagination={{ pageSize: 5, showQuickJumper: true }}
                />

                {/*  */}
                {
                    showLinkButton === 1 &&
                    <Modal
                        title=""
                        visible={showLinkButton === 1}
                        onOk={() => { this.handleAdd() }}
                        confirmLoading={confirmLoading}
                        onCancel={this.handleCancel}
                    >
                        <Form ref={this.addRef}>
                            <Form.Item
                                name="parentId"
                                label=""
                                initialValue={parentId}
                                hasFeedback
                                rules={[{ required: true, message: '!' }]}
                            >
                                <Select placeholder="">
                                    <Option key="0" value="0"></Option>
                                    {
                                        dataSource.map(item => {
                                            return <Option key={item._id} value={item._id}>{item.name}</Option>
                                        })
                                    }
                                </Select>
                            </Form.Item>

                            <Form.Item
                                name="categoryName"
                                label=""
                                hasFeedback
                                rules={[
                                    {
                                        required: true,
                                        message: '',
                                    },
                                ]}
                            >
                                <Input placeholder="" />
                            </Form.Item>
                        </Form>
                    </Modal>
                }
                {/*  */}
                <Modal
                    title=""
                    visible={showLinkButton === 2}
                    onOk={this.handleUpdate}
                    confirmLoading={confirmLoading}
                    onCancel={this.handleCancel}
                    destroyOnClose      // 
                >
                    <Form ref={this.updateRef}>
                        <Form.Item
                            name="categoryName"
                            label=""
                            hasFeedback
                            initialValue={this.category === undefined ? null : this.category.name}
                            rules={[
                                {
                                    required: true,
                                    message: '',
                                },
                            ]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Form>
                </Modal>
            </Card>
        )
    }
}
