import React, { Component } from 'react';
import PropTypes from 'prop-types'
import { Upload, Modal, message } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import { BASE_IMG_PATH, UPLOAD_IMG_NAME } from '../../utils/constants';
import { reqDeleteProductImg } from '../../api'

export default class PicturesWall extends Component {
  static propTypes = {
    // 
    img: PropTypes.array
  }
  // fileList
  constructor(props) {
    super(props);
    let fileList = [];
    //  imgs,  fileList 
    const { imgs } = this.props;
    // imgs
    if (imgs && imgs.length > 0) {
      fileList = imgs.map((img, index) => ({
        uid: -index,
        name: img,
        status: 'done', // loading: , done: , remove:  
        url: BASE_IMG_PATH + img,
      }))
    }
    this.state = {
      previewVisible: false,      // 
      previewImage: '',           // 
      fileList
    };
  }

  // 
  getImgs = () => this.state.fileList.map(file => file.name);

  // 
  handleCancel = () => this.setState({ previewVisible: false });

  // 
  handlePreview = file => {
    this.setState({
      previewImage: file.url || file.thumbUrl,    // thumbUrl:
      previewVisible: true,
    });
  };
  // 
  // 3filefileListevent
  handleChange = async ({ file, fileList }) => {
    console.log(file)
    // 
    if (file.status === 'done') {
      // result 
      // {state: 0 data: {name: , url: }}
      const result = file.response;
      if (result.status === 0) {
        message.success('');
        // file
        const { name, url } = result.data;
        file = fileList[fileList.length - 1];
        file.name = name;
        file.url = url;
      } else {
        message.error('');
      }
    } else if (file.status === 'removed') {
      const result = await reqDeleteProductImg(file.name);
      if (result.status === 0) {
        message.success('');
      } else {
        message.error('');
      }
    }
    //  fileList  
    this.setState({ fileList })
  };
  render() {
    const { previewVisible, previewImage, fileList } = this.state;
    // 
    const uploadButton = (
      <div>
        <PlusOutlined />
        <div style={{ marginTop: 8 }}>Upload</div>
      </div>
    );
    return (
      <>
        <Upload
          action="/manage/img/upload"        //  
          accept="image/*"                   // 
          name={UPLOAD_IMG_NAME}             // image
          listType="picture-card"            // 
          fileList={fileList}                // 
          onPreview={this.handlePreview}
          onChange={this.handleChange}
          beforeUpload={this.beforeUpload}
        >
          {/* 4 */}
          {fileList.length >= 4 ? null : uploadButton}
        </Upload>
        {/*  */}
        <Modal
          visible={previewVisible}
          footer={null}
          onCancel={this.handleCancel}
        >
          <img alt="product-img" style={{ width: '100%' }} src={previewImage} />
        </Modal>
      </>
    );
  }
}