import React, {Component} from 'react'
import {
  Card,
  Statistic,
  DatePicker,
  Timeline
} from 'antd'
import {
  QuestionCircleOutlined,
  ArrowDownOutlined,
  ArrowUpOutlined,
  ReloadOutlined
} from '@ant-design/icons'

import moment from 'moment'

import LineChart from './Line'
import Bar from './Bar'
import './index.less'

const dateFormat = 'YYYY/MM/DD'
const {RangePicker} = DatePicker

export default class Home extends Component {

  state = {
    isVisited: true
  }

  handleChange = (isVisited) => {
    return () => this.setState({isVisited})
  }

  render() {
    const {isVisited} = this.state

    return (
      <div className='home'>
        <Card
          className="home-card"
          title=""
          extra={<QuestionCircleOutlined style={{color: 'rgba(0,0,0,.45)'}}/>}
          style={{width: 250}}
          headStyle={{color: 'rgba(0,0,0,.45)'}}
        >
          <Statistic
            value={1128163}
            suffix=""
            style={{fontWeight: 'bolder'}}
          />
          <Statistic
            value={15}
            valueStyle={{fontSize: 15}}
            prefix={''}
            suffix={<div>%<ArrowDownOutlined style={{color: 'red', marginLeft: 10}} /></div>}
          />
          <Statistic
            value={10}
            valueStyle={{fontSize: 15}}
            prefix={''}
            suffix={<div>%<ArrowUpOutlined style={{color: '#3f8600', marginLeft: 10}} /></div>}
          />
        </Card>

        <LineChart/>

        <Card
          className="home-content"
          title={<div className="home-menu">
            <span className={isVisited ? "home-menu-active home-menu-visited" : 'home-menu-visited'}
                  onClick={this.handleChange(true)}></span>
            <span className={isVisited ? "" : 'home-menu-active'} onClick={this.handleChange(false)}></span>
          </div>}
          extra={<RangePicker
            defaultValue={[moment('2021/07/30', dateFormat), moment('2021/08/16', dateFormat)]}
            format={dateFormat}
          />}
        >
          <Card
            className="home-table-left"
            title={isVisited ? '' : ''}
            bodyStyle={{padding: 0, height: 275}}
            extra={<ReloadOutlined />}
          >
            <Bar/>
          </Card>

          <Card title='' extra={<ReloadOutlined />} className="home-table-right">
            <Timeline>
              <Timeline.Item color="green"></Timeline.Item>
              <Timeline.Item color="green"></Timeline.Item>
              <Timeline.Item color="red">
                <p></p>
                <p></p>
              </Timeline.Item>
              <Timeline.Item>
                <p></p>
                <p></p>
                <p></p>
              </Timeline.Item>
            </Timeline>
          </Card>
        </Card>
      </div>
    )
  }
}