import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import {connect} from 'react-redux';
// 
import { Modal } from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons';
// 
import { reqWeather } from '../../api';
import { formateDate } from '../../utils/dateUtils';
// 
// import menuList from '../../config/menuConfig';
// 
// import storageUtils from '../../utils/storageUtils';
// import memoryUtils from '../../utils/memoryUtils';

import LinkButton from '../LinkButton';
// action
import { logout } from '../../redux/action';
import './index.less';

const { confirm } = Modal;
class Header extends Component {
    state = {
        city: '',
        weather: '',
        temperature: '',
        sysTime: formateDate(Date.now())
    }
    // 
    getWeather = async () => {
        const data = await reqWeather();
        // 
        if (data.status === "1") {
            const { city, weather, temperature } = data.lives[0];
            this.setState({ city, weather, temperature });
        }
    }
    // 
    getSysTime = () => {
        this.interValId = setInterval(() => {
            this.setState({ sysTime: formateDate(Date.now()) })
        }, 1000)
    }

    // path
    // getTitle = () => {
    //     let path = this.props.location.pathname;
    //     let title;
    //     menuList.forEach(item => {
    //         if (item.key === path) {
    //             title = item.title;
    //         } else if (item.children) {
    //             item.children.forEach(citem => {
    //                 // header
    //                 if (path.indexOf('/product') === 0) {
    //                     path = '/product';
    //                 };
    //                 if (citem.key === path) {
    //                     title = citem.title;
    //                 }
    //             })
    //         }
    //     })
    //     return title;
    // }

    // 
    logout = () => {
        confirm({
            title: '?',
            icon: <ExclamationCircleOutlined />,
            content: '',
            // onOKthis.propsthis
            onOk: () => {
                // // localStorage
                // storageUtils.removeUser();
                // // memoryUtils
                // memoryUtils.user = {};
                
                // action
                this.props.logout();
                // login
                this.props.history.replace('/login');
            },
        });
    }
    // 
    componentDidMount() {
        this.getWeather();
        this.getSysTime();
    }
    // 
    componentWillUnmount() {
        clearInterval(this.interValId);
    }
    render() {
        const { city, weather, temperature, sysTime } = this.state;
        
        // // 
        // const user = memoryUtils.user.username;
        // // 
        // const title = this.getTitle(path);

        // store
        const user = this.props.user.username;
        const title = this.props.headTitle;
        
        return (
            <div className="header-box">
                <div className="header-top">
                    <span>Hello, {user}</span>
                    <LinkButton onClick={this.logout}></LinkButton>
                </div>
                <div className="header-bottom">
                    <div className="path-box">{title}</div>
                    <div className="weather-box">
                        <span className="date">{sysTime}</span>
                        <span className="distinct">{city}</span>
                        <span className="weather">{weather} &nbsp;{temperature}</span>
                    </div>
                </div>
            </div>
        )
    }
}
export default connect(
    state => ({
        user: state.user, 
        headTitle: state.headTitle
    }),
    {
        logout
    }
)(withRouter(Header));