//  menuList
import React from 'react'
import {
    PieChartOutlined,
    LineChartOutlined,
    BarChartOutlined,
    AreaChartOutlined,
    SafetyOutlined,
    ToolOutlined,
    BarsOutlined,
    AppstoreOutlined,
    HomeOutlined,
    UserOutlined
} from '@ant-design/icons'

export default [{
    title: '',    //  
    key: '/home',    //  path 
    icon: (<HomeOutlined />),    //  
    isPublic: true, // 
    disabled: true  // 
    
}, {
    title: '',
    key: '/products',
    icon: (<AppstoreOutlined />),
    children: [
        //  
        {
            title: '',
            key: '/category',
            icon: (<BarsOutlined />)
        }, {
            title: '',
            key: '/product',
            icon: (<ToolOutlined />)
        },
    ]
}, {
    title: '',
    key: '/user',
    icon: (<UserOutlined />)
}, {
    title: '',
    key: '/role',
    icon: (<SafetyOutlined />)
}, {
    title: '',
    key: '/charts',
    icon: (<AreaChartOutlined />),
    children: [{
        title: '',
        key: '/charts/bar',
        icon: (<BarChartOutlined />)
    }, {
        title: '',
        key: '/charts/line',
        icon: (<LineChartOutlined />)
    }, {
        title: '',
        key: '/charts/pie',
        icon: (<PieChartOutlined />)
    }]
} ]